@import url("https://fonts.cdnfonts.com/css/avenir");

* {
  margin: 0;
  padding: 0;
  font-family: arial;
  box-sizing: border-box;
}

body {
  /* background-image: url("/public/images/wallpaper.jpg");
  background-repeat: repeat-y; */
  background-color: rgb(157, 194, 218);
}

main {
  min-height: calc(100vh - 80px);
}

.loader {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

footer {
  position: relative;
  height: 5vh;
  min-height: 40px;
  width: 100%;
  background: linear-gradient(
    72deg,
    rgba(32, 105, 153, 1) 0%,
    rgba(126, 189, 227, 1) 48%,
    rgba(74, 168, 205, 1) 87%
  );

  font-size: 20px;
  color: white;
  text-align: center;
  vertical-align: middle;
  font-family: "Avenir";
  font-weight: bold;
}

footer p {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
